$font-size: 20px;

html body {
  margin: 0;
  background-color: rgb(40, 40, 40);
  width: 100%;
  height: 100%;
  color: whitesmoke;
  font-size: $font-size;
}

@media only screen and (min-width: 768px) {
  .answerInput {
    width: 500px;
  }
}

#mainContainer {
  padding: 1em;
}

.inputLabel {
  padding: 10px 10px 10px 0;
  width: 50px;
}

.rangeInput {
  height: 30px;
  font-size: $font-size;
  padding: 5px;
}

.button {
  margin: 10px 10px 10px 0;
  width: 80px;
  height: 40px;
  display: block;
  font-size: $font-size;
  user-select: none;
}

.currentWord {
  color: green;
  font-size: 32px;
  padding: 0 10px 0 10px;
  font-family: "Adobe Minion Cyrillic";
}

.answerInput {
  width: 100%;
  height: 30px;
  font-size: $font-size;
}

input {
  border-radius: 5px;
  $font-size: $font-size;
}

.invertedContainer {
  padding: 10px 10px 10px 0;
}

.selectInverted {
  height: 20px;
  width: 20px;
  vertical-align: -2px;
  margin-left: 10px;
}

.informationButton {
  margin-bottom: 20px;
  width: 30px;
  height: 30px;
  border-radius: 100px;
}

.informationDisplay {
  position: relative;
  width: 90%;
  height: 120px;
}

.hiddenElement {
  display: none;
}
